.primary {
    background-color: #662D91 !important;
}

.color-primary {
    color: #662D91 !important;
    font-weight: bolder;
}

.primary-container {
    background-color: #662D91;
    color: #BFBFBF;
}

.reverse {
    color: #662D91;
    background-color: #BFBFBF;
}

.secondary {
    background-color: #190826 !important;
}

.navbar {
    border-bottom: 5px solid whitesmoke;
}

.nav-item:hover {
    cursor: pointer;
}

#root {
    background-color: whitesmoke;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

.body-container {
    flex: 1 0 auto;
}

.footer {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    border-top: 3px solid grey;
}

.footer-content {
    text-align: center;
    color: ghostwhite;
}

@media screen and (max-width: 767px){
    .footer {
        padding-top: 0;
        padding-bottom: 0;
    }

    .footer-content {
        padding-top: 1rem;
        padding-bottom: 1rem;
    }
}

.dropdown-menu {
    padding: 0.5rem;
    min-width: 250px;
}

.redirect {
    cursor: pointer;
    color: #662D91;
}

.redirect:hover {
    color: rgba(102, 45, 145, 0.7);
}

.b-secondary {
    border: 1px solid #190826;
}

body {
    font-size: 160%;
    font-weight: lighter;
}

.navbar-brand, .fs-140 {
    font-size: 140%;
}

.greyscale {
    -webkit-transition: ease-in-out all 0.5s;
    transition: ease-in-out all 0.5s;
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
    -moz-filter: grayscale(100%);
    -ms-filter: grayscale(100%);
    -o-filter: grayscale(100%);
}

.greyscale:hover {
    -webkit-transition: ease-in-out all 0.5s;
    transition: ease-in-out all 0.5s;
    filter: grayscale(0%);
    -webkit-filter: grayscale(0%);
    -moz-filter: grayscale(0%);
    -ms-filter: grayscale(0%);
    -o-filter: grayscale(0%);
}

.fs-600 {
    font-size: 600%;
}

.fsr-5 {
    font-size: 5rem;
}

.dropdown-item {
    font-size: 140%;
}

.dropdown-item:active {
    color: #662D91;
    background-color: transparent;
}

.b-ws {
    background-color: whitesmoke;
}

.bg-title_outing {
    padding-top: 2rem !important;
    padding-bottom: 2rem !important;
    background-color: #23022E;
}

.no-style {
    list-style: none;
}

@media screen and (min-width: 768px){
    .b-right-md {
        border-right: 1px solid darkgrey;
    }

    .b-left-md {
        border-left: 1px solid darkgrey;
    }
}

.required {
    color: darkred;
}

.title-cgu {
    text-decoration: underline;
}

.logo-navbar {
    max-height: 50px;
}

.react-datepicker-wrapper, .react-datepicker__input-container {
    display: block !important;
}

.card-body {
    position: relative;
    z-index: 5;
    height: 100%;
    max-width: 100%;
    padding: 20px;
}

.bg-outing {
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url("https://images.unsplash.com/photo-1524368535928-5b5e00ddc76b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2100&q=80") center center;
    opacity: 0.4;
    width: 100%;
    height: 100%;
}

.card-header {
    padding: 0;
    font-size: 2rem;
}

.card-header-secondary {
    padding: 0 0 1.25rem 0;
    font-size: 1rem;
}

.city-show {
    position: absolute;
    top: 50%;
    right: 0;
    font-size: 200%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color: #3F3F3F;
}

.p-relative {
    position: relative;
}

.x5 {
    width: 5rem;
    height: 5rem;
}

.return-to-top-container {
    background-color: rgba(150, 150, 150, 0.5);
    display: flex;
    z-index: 3;
    cursor: pointer;
    color: white;
    position: fixed;
    height: 30px;
    width: 30px;
    font-size: 2rem;
    bottom: 50px;
    right: 50px;
    border-radius: 5px;
}

.return-to-top-container > span {
    margin: auto;
}

@media screen and (max-width: 767px){
    .return-to-top-container {
        bottom: 50px;
        right: 20px;
    }
}

.return-to-top-container:hover {
    background-color: rgba(50, 50, 50, 0.7);
}

.truncate-more-less:hover {
    cursor: pointer;
}

.carousel .control-arrow,
.carousel.carousel-slider .control-arrow {
    -webkit-transition: all .25s ease-in;
    transition: all .25s ease-in;
    opacity: .4;
    position: absolute;
    z-index: 2;
    top: 20px;
    background: 0 0;
    border: 0;
    font-size: 32px;
    cursor: pointer
}

.carousel .control-arrow:hover {
    opacity: 1
}

.carousel .control-arrow:before,
.carousel.carousel-slider .control-arrow:before {
    margin: 0 5px;
    display: inline-block;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    content: ''
}

.carousel .control-disabled.control-arrow {
    opacity: 0;
    cursor: inherit;
    display: none
}

.carousel .control-prev.control-arrow {
    left: 0
}

.carousel .control-prev.control-arrow:before {
    border-right: 8px solid #fff
}

.carousel .control-next.control-arrow {
    right: 0
}

.carousel .control-next.control-arrow:before {
    border-left: 8px solid #fff
}

.carousel {
    position: relative;
    width: 100%
}

.carousel * {
    box-sizing: border-box
}

.carousel > .slider-wrapper > ul {
    width: 100%;
    display: inline-block;
    pointer-events: none
}

@media screen and (min-width: 768px) and (max-width: 991px){
    .carousel > .slider-wrapper > ul {
        width: 50%;
    }
}

@media screen and (max-width: 767px){
    .carousel > .slider-wrapper > ul {
        width: 100%;
    }
}

.carousel .carousel {
    position: relative
}

.carousel .control-arrow {
    outline: 0;
    border: 0;
    background: 0 0;
    top: 50%;
    margin-top: -13px;
    font-size: 18px
}

.carousel .thumbs-wrapper {
    margin: 20px;
    overflow: hidden
}

.carousel .thumbs {
    -webkit-transition: all .15s ease-in;
    transition: all .15s ease-in;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    position: relative;
    list-style: none;
    white-space: nowrap
}

.carousel .thumb {
    -webkit-transition: border .15s ease-in;
    transition: border .15s ease-in;
    display: inline-block;
    width: 80px;
    margin-right: 6px;
    white-space: nowrap;
    overflow: hidden;
    border: 3px solid #fff;
    padding: 2px
}

.carousel .thumb:focus {
    border: 3px solid #ccc;
    outline: 0
}

.carousel .thumb.selected,
.carousel .thumb:hover {
    border: 3px solid #333
}

.carousel .thumb img {
    vertical-align: top
}

.carousel.carousel-slider {
    position: relative;
    margin: 0;
    overflow: hidden
}

.carousel.carousel-slider .control-arrow {
    top: 0;
    color: #fff;
    font-size: 26px;
    bottom: 0;
    margin-top: 0;
    padding: 5px
}

.carousel.carousel-slider .control-arrow:hover {
    background: rgba(0, 0, 0, .2)
}

.carousel .slider-wrapper {
    overflow: hidden;
    margin: auto;
    width: 100%;
    -webkit-transition: height .15s ease-in;
    transition: height .15s ease-in
}

.carousel .slider-wrapper.axis-horizontal .slider {
    -ms-box-orient: horizontal;
    display: -moz-flex;
    display: flex
}

.carousel .slider-wrapper.axis-horizontal .slider .slide {
    flex-direction: column;
    flex-flow: column
}

.carousel .slider-wrapper.axis-vertical {
    -ms-box-orient: horizontal;
    display: -moz-flex;
    display: flex
}

.carousel .slider-wrapper.axis-vertical .slider {
    flex-direction: column
}

.carousel .slider {
    margin: 0;
    padding: 0;
    position: relative;
    list-style: none;
    width: 100%
}

.carousel .slider.animated {
    -webkit-transition: all .35s ease-in-out;
    transition: all .35s ease-in-out
}

.carousel .slide {
    min-width: 100%;
    margin: 0;
    position: relative;
    text-align: center;
}

.carousel .slide img {
    width: 100%;
    vertical-align: top;
    border: 0
}

.carousel .slide iframe {
    display: inline-block;
    width: calc(100% - 80px);
    margin: 0 40px 40px;
    border: 0
}

.carousel .slide .legend {
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
    position: absolute;
    bottom: 40px;
    left: 50%;
    margin-left: -45%;
    width: 90%;
    border-radius: 10px;
    background: #000;
    color: #fff;
    padding: 10px;
    font-size: 12px;
    text-align: center;
    opacity: .25;
    -webkit-transition: opacity .35s ease-in-out;
    transition: opacity .35s ease-in-out
}

.carousel .control-dots {
    position: absolute;
    bottom: 0;
    margin: 10px 0;
    text-align: center;
    width: 100%
}

@media (min-width:960px) {
    .carousel .control-dots {
        bottom: 0
    }
}

.carousel .control-dots .dot {
    -webkit-transition: opacity .25s ease-in;
    transition: opacity .25s ease-in;
    opacity: .3;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, .9);
    background: #fff;
    border-radius: 50%;
    width: 8px;
    height: 8px;
    cursor: pointer;
    display: inline-block;
    margin: 0 8px
}

.carousel .control-dots .dot.selected,
.carousel .control-dots .dot:hover {
    opacity: 1
}

.carousel .carousel-status {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    font-size: 10px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .9);
    color: #fff
}

.carousel:hover .slide .legend {
    opacity: 1
}

.carousel-images {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
}

.slide, .carousel-images {
    height: 75vh;
}

